import Background from '../images/fragile.png'

const styles = theme => ({
  list: {
    height: '635px',
    overflow: 'auto'
  },
  text: {
    fontFamily: 'Verdana',
  },
  iconSmall: {
    fontSize: '12px',
  },
  iconLarge: {
    fontSize: '18px',
  },
  autoRefresh: {
    padding: '0 10px 0 0',
    fontStyle: 'italic',
    color: '#607D8B'
  },
  textOwner: {
    fontFamily: 'Verdana',
    fontStyle: 'italic',
    color: '#1A237E',
    padding: '0 0 0 10px',
  },
  background: {
    backgroundImage: "url(" + Background + ")",
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    //backgroundColor: 'rgba(52, 52, 52, 0.8)',
    //backgroundColor: 'transparent',
    //backgroundColor: '#00000000',
    overflow: 'hidden',
  }
});

export default styles
